export default {
	data() {
		return {
			details:{},
			user:{}
		}
	},
	onLoad(options) {
		console.log(options);
		this.$api.auth.identify().then(data => {
			console.log(data)
			this.user = data;
		})
		this.$api.auth.finances().then(data => {
			console.log(data);
			this.details = data.results;
		})
	},
	methods:{
		navToRecharge(user_id) {
			console.log(user_id);
			let id = user_id;
			uni.navigateTo({
				url: `/pages/user/recharge?id=${id}`
			})
		},
	}
}
